import { replacePlaceholder } from "@rp/common/utils"
import { WorkflowState } from "./Workflow.types"
import { generateWorkflowTitle } from "./generateWorkflowTitle"

export const generatePromptFromWorkflow = (workflow: WorkflowState) => {
  return {
    prompt: replacePlaceholder(workflow.originWorkflow.input_message, {
      entity: workflow.selectedData?.selectedCompanies?.[0]?.name ?? "",
      sector: workflow.selectedData?.selectedSector ?? "",
    }),
    customTitle: generateWorkflowTitle(
      workflow.originWorkflow.flowName,
      workflow.selectedData?.selectedCompanies?.[0]?.name,
    ),
    replaceState: true,
    interactionType: "flow" as const,
    entities: workflow.selectedData?.selectedCompanies,
  }
}
